<template>
  <Modal :title="title" v-model="showModal" :width="1000" class-name="vertical-center-modal singleUserMap">
    <viMap @onMount="onMount" class="map"></viMap>
    <template #footer>
      <template v-if="isEdit">
        <Button type="text" @click="showModal = false">取消</Button>
        <Button type="primary" @click="$emit('confirm', coordinate)">确定</Button>
      </template>
      <Button type="primary" @click="toMapNavigation" v-else>去导航</Button>
    </template>
  </Modal>
</template>

<script>
import { viMap } from "@/components";
import { Vector as VectorLayer } from "ol/layer";
import { Vector as VectorSource } from "ol/source";
import Feature from "ol/Feature";
import { Icon, Style } from "ol/style";
import { Translate } from "ol/interaction";
import { Point } from "ol/geom";
import POINT from "@/assets/images/point_red.png";
import { getMapNavigation } from "@/utils";

export default {
  name: "singleUserMap",
  components: { viMap },
  props: {
    modelValue: {
      type: Boolean,
      default: false,
    },
    curRow: {
      type: Object,
      default() {
        return {};
      }
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    const webConfig = JSON.parse(sessionStorage.getItem("webConfig") || "{}");
    return {
      mapConfig: webConfig.map || {},
      map: null,
      vectorSource: null,
      coordinate: [],
    };
  },
  computed: {
    showModal: {
      get() {
        return this.modelValue;
      },
      set(val) {
        this.$emit("update:modelValue", val);
      }
    },
    title() {
      return `${this.isEdit ? "点击/拖拽选择" : "查看"} ${this.curRow.name} 定位`;
    },
  },
  watch: {
    showModal(val) {
      if (val) {
        this.drawPoint(this.curRow, true);
      }
    },
  },
  methods: {
    drawPoint({ longitude, latitude }, setCenter) {
      this.vectorSource.clear();
      if (!longitude || !latitude) {
        this.map.getView().setCenter(this.mapConfig.center);
        return;
      }
      this.coordinate = [+longitude.toFixed(6), +latitude.toFixed(6)];
      const feature = new Feature({
        geometry: new Point(this.coordinate),
      });
      this.vectorSource.addFeature(feature);
      if (setCenter) {
        this.map.getView().setCenter(this.coordinate);
      }
    },
    onMount(map) {
      this.map = map;
      this.vectorSource = new VectorSource();
      const vectorLayer = new VectorLayer({
        source: this.vectorSource,
        style: () => new Style({
          image: new Icon({
            src: POINT,
            anchor: [0.5, 1],
          }),
        }),
      });
      this.map.addLayer(vectorLayer);
      if (this.isEdit) {
        this.map.on("singleclick", (e) => {
          const [longitude, latitude] = e.coordinate;
          this.drawPoint({ longitude, latitude });
        });
        const verifyTranslate = new Translate({
          layers: [vectorLayer],
        });
        this.map.addInteraction(verifyTranslate);
        verifyTranslate.on("translateend", (e) => {
          const [longitude, latitude] = e.features.array_[0].getGeometry().flatCoordinates;
          this.coordinate = [+longitude.toFixed(6), +latitude.toFixed(6)];
        });
      }
    },
    toMapNavigation() {
      const url = getMapNavigation(this.curRow);
      if (!url) return this.$Message.error("未找到定位");
      window.open(url, "_self");
    },
  },
};
</script>

<style lang="less" scoped>
.map {
  height: 70vh;
}
</style>
<style lang="less">
.singleUserMap {
  .ivu-modal-body {
    padding: 0;
  }
}
</style>
