<template>
  <Modal :title="title" v-model="showModal" :width="400" class-name="vertical-center-modal">
    <Form ref="form" :model="formData" :rules="rules" :label-width="90" @keydown.enter="submit" class="plr">
      <FormItem label="客户名称:" prop="name">
        <viInput v-model="formData.name"></viInput>
      </FormItem>
      <FormItem label="联系电话:" prop="phone">
        <viInput v-model="formData.phone"></viInput>
      </FormItem>
      <FormItem label="备注:" prop="remarks">
        <viInput v-model="formData.remarks" type="textarea"></viInput>
      </FormItem>
      <FormItem label="定位:">
        <div class="flex-ac">
          <img src="~@/assets/images/point_blue.png" @click="showMapModal = true" class="pointer mr-10">
          <span v-if="formData.longitude && formData.latitude">{{ formData.longitude }},{{ formData.latitude }}</span>
        </div>
      </FormItem>
    </Form>
    <single-user-map v-model="showMapModal" :curRow="formData" isEdit @confirm="changeLonLat"></single-user-map>
    <template #footer>
      <Button type="text" @click="showModal = false">取消</Button>
      <Button type="primary" @click="submit" :loading="loading">确定</Button>
    </template>
  </Modal>
</template>

<script>
import { saveUsers, verifyUsers } from "@/axios";
import clone from "clone";
import singleUserMap from "./singleUserMap";

export default {
  name: "addUserModal",
  components: { singleUserMap },
  props: {
    modelValue: {
      type: Boolean,
      default: false,
    },
    detailData: {
      type: Object,
      default() {
        return {};
      }
    },
    showCount: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    const verifyName = async (rule, value, callback) => {
      if (value && this.detailData.name !== this.formData.name) {
        const { data } = await verifyUsers({
          name: value,
        }) || {};
        if (data) {
          return callback(new Error("该客户名称已存在"));
        }
      }
      callback();
    };
    return {
      formData: {},
      rules: {
        name: [
          { required: true, message: "请输入客户名称" },
          { validator: verifyName, trigger: "blur", }
        ],
      },
      loading: false,
      showMapModal: false,
    };
  },
  computed: {
    title() {
      return `${!this.detailData.userId ? "新增" : "编辑"}客户`;
    },
    showModal: {
      get() {
        return this.modelValue;
      },
      set(val) {
        this.$emit("update:modelValue", val);
        if (!val) {
          this.$emit("close");
        }
      }
    },
  },
  watch: {
    detailData: {
      deep: true,
      handler() {
        this.$refs.form.resetFields();
        this.formData = clone(this.detailData);
      },
    }
  },
  methods: {
    changeLonLat([longitude, latitude]) {
      this.formData = {
        ...this.formData,
        longitude,
        latitude,
      };
      this.showMapModal = false;
    },
    submit() {
      this.loading = true;
      this.$refs.form.validate(async (valid) => {
        if (!valid) {
          this.$Message.error("数据校验不通过");
          this.loading = false;
          return;
        }
        try {
          const { _index, _rowKey, count, ...params } = this.formData;
          const { data } = await saveUsers(params) || {};
          if (data) {
            this.$Message.success(`${this.title}成功`);
            this.$emit("save", { ...this.formData });
            this.$refs.form.resetFields();
            this.showModal = false;
          }
        } catch (err) {
          this.$Message.error(`请求失败：${err.message || err}`);
        } finally {
          this.loading = false;
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>

</style>
