<template>
  <viMap @onMount="onMount" class="h100">
    <Button type="success" @click="$emit('close')" class="list-mode" v-if="!isMobile">列表模式</Button>
    <Select v-model="curUserId" ref="user" filterable clearable class="select-user">
      <Option
        v-for="item in allUsers"
        :key="item.userId"
        :value="item.userId"
      >{{ item.name }}</Option>
    </Select>
    <div ref="userPopup">
      <div class="popup">
        <div class="popup-header">
          <span class="title">{{ curUser.name }}</span>
          <span class="close" @click="userOverlay.setPosition(undefined)"> X </span>
        </div>
        <div class="popup-content">
          <p>电话：{{ curUser.phone || "-" }}</p>
          <p class="mb-10">备注：{{ curUser.remarks || "-" }}</p>
          <Button type="primary" @click="$emit('changeUser', curUser)" size="small">修改</Button>
          <Button type="error" @click="$emit('deleteUser', curUser)" size="small" class="ml-10">删除</Button>
          <div class="mt-10"></div>
          <Button type="success" @click="$emit('openSpecialMng', curUser)" size="small">特价</Button>
          <Button type="primary" @click="$emit('openHistoryOrder', curUser)" size="small" class="ml-10">历史订单</Button>
          <div class="mt-10"></div>
          <Button type="primary" @click="toMapNavigation" size="small">导航</Button>
          <Button @click="verifyLonlat" size="small" class="ml-10">校准</Button>
        </div>
      </div>
    </div>
    <div ref="clusterPopup">
      <div class="popup">
        <div class="popup-header">
          <span class="title">聚合点位</span>
          <span class="close" @click="clusterOverlay.setPosition(undefined)"> X </span>
        </div>
        <div class="popup-content">
          <p
            v-for="(item, index) in userList"
            :key="index"
            @click="showUserDetail(item)"
            class="pointer"
          >{{ index + 1 }}、{{ item.name }}</p>
        </div>
      </div>
    </div>
    <div ref="emptyPopup">
      <div class="popup">
        <div class="popup-content tc">
          <template v-if="!showVerify">
            <Button type="success" @click="$emit('changeUser', curUser)" size="small">{{ curUserId ? "定位" : "新增" }}</Button>
            <Button type="primary" @click="toMapNavigation" size="small" class="ml-10">导航</Button>
          </template>
          <template v-else>
            <Button @click="closeEmptyOverlay" size="small">取消</Button>
            <Button type="success" @click="confirmVerify" size="small" class="ml-10">确认</Button>
          </template>
          <span @click="closeEmptyOverlay" class="ml-10 pointer"> X </span>
        </div>
      </div>
    </div>
  </viMap>
</template>

<script>
import { Vector as VectorLayer } from "ol/layer";
import { Vector as VectorSource, Cluster } from "ol/source";
import Feature from "ol/Feature";
import Overlay from "ol/Overlay";
import { Icon, Style, Text, Fill, Stroke, Circle as CircleStyle } from "ol/style";
import { Translate } from "ol/interaction";
import Collection from "ol/Collection";
import { Point } from "ol/geom";
import POINT from "@/assets/images/point_red.png";
import { getAllUsers, saveUsers } from "@/axios";
import { viMap } from "@/components";
import { getMapNavigation } from "@/utils";

export default {
  name: "allUsersMap",
  components: { viMap },
  data() {
    return {
      isMobile: window.isMobile,
      allUsers: [],
      map: null,
      layer: null,
      verifyLayer: null,
      verifyTranslate: null,
      userOverlay: null,
      clusterOverlay: null,
      emptyOverlay: null,
      curUser: {},
      userList: [],
      showVerify: false,
      curUserId: "",
    };
  },
  watch: {
    curUserId(val) {
      const curUser = this.allUsers.find((item) => item.userId === val);
      this.userOverlay.setPosition(undefined);
      if (curUser && curUser.longitude && curUser.latitude) {
        this.curUser = curUser;
        const coordinate = [curUser.longitude, curUser.latitude];
        this.map.getView().animate({
          center: coordinate,
        });
        this.userOverlay.setPosition(coordinate);
      } else if (curUser) {
        this.curUser = curUser;
        this.$Message.info("该用户未设置定位<br>点击地图进行定位");
      } else {
        // 如果有经纬度就保留，删掉其他值
        this.curUser = {
          longitude: this.curUser.longitude,
          latitude: this.curUser.latitude,
        };
      }
    },
  },
  methods: {
    verifyLonlat() {
      if (this.layer) {
        this.layer.getSource().getSource().clear();
        this.map.removeLayer(this.layer);
        this.layer = null;
      }
      this.userOverlay.setPosition(undefined);
      const feature = new Feature({
        geometry: new Point([this.curUser.longitude, this.curUser.latitude]),
      });
      const source = new VectorSource({
        features: [feature],
      });
      this.verifyLayer = new VectorLayer({
        source,
        style: new Style({
          image: new Icon({
            src: POINT,
            anchor: [0.5, 1],
          }),
        }),
      });
      this.map.addLayer(this.verifyLayer);
      const select = new Collection([feature]);
      this.verifyTranslate = new Translate({
        features: select,
      });
      this.map.addInteraction(this.verifyTranslate);
      this.verifyTranslate.on("translatestart", () => {
        this.showVerify = true;
        this.emptyOverlay.setPosition(undefined);
      });
      this.verifyTranslate.on("translateend", (e) => {
        const coordinate = e.features.array_[0].getGeometry().flatCoordinates;
        this.emptyOverlay.setPosition(coordinate);
        this.curUser = {
          ...this.curUser,
          longitude: +coordinate[0].toFixed(6),
          latitude: +coordinate[1].toFixed(6),
        };
      });
    },
    closeEmptyOverlay() {
      this.emptyOverlay.setPosition(undefined);
      if (this.verifyLayer) {
        this.map.removeLayer(this.verifyLayer);
        this.verifyLayer = null;
        this.map.removeInteraction(this.verifyTranslate);
        this.verifyTranslate = null;
      }
      if (this.showVerify) {
        this.showVerify = false;
        this.drawCluster();
      }
    },
    async confirmVerify() {
      try {
        const { data } = await saveUsers(this.curUser) || {};
        if (data) {
          this.$Message.success("校准成功");
          this.closeEmptyOverlay();
          this.getUsersList();
        }
      } catch (err) {
        this.$Message.error(`校准失败：${err.message || err}`);
      }
    },
    toMapNavigation() {
      const url = getMapNavigation(this.curUser);
      if (!url) return this.$Message.error("未找到定位");
      window.open(url, "_self");
    },
    showUserDetail(data) {
      this.curUser = { ...data };
      this.clusterOverlay.setPosition(undefined);
      this.userOverlay.setPosition([data.longitude, data.latitude]);
    },
    drawCluster() {
      if (!this.map) return;
      if (this.layer) {
        this.layer.getSource().getSource().clear();
        this.map.removeLayer(this.layer);
        this.layer = null;
      }
      const features = this.allUsers
        .filter((item) => item.longitude && item.latitude)
        .map((item) =>
          new Feature({
            geometry: new Point([item.longitude, item.latitude]),
            data: item,
          }));
      const clusterSource = new Cluster({
        distance: 20, // 距离，单位px
        source: new VectorSource({ features }),
      });
      this.layer = new VectorLayer({
        source: clusterSource,
        style: (feature) => this.setClusterStyle(feature),
      });
      this.map.addLayer(this.layer);
    },
    setClusterStyle(feature) {
      const total = feature.get("features").length;
      if (total == 1) {
        return new Style({
          image: new Icon({
            src: POINT,
            anchor: [0.5, 1],
          }),
        });
      } else {
        return new Style({
          image: new CircleStyle({
            radius: 16,
            stroke: new Stroke({
              color: "#fff",
              width: 1,
            }),
            fill: new Fill({
              color: "#6086FF",
            }),
          }),
          text: new Text({
            text: total.toString(),
            font: "12px sans-serif",
            fill: new Fill({
              color: "#FFFFFF",
            }),
          }),
          zIndex: total,
        });
      }
    },
    mapClickHandle(e) {
      // 如果点击的是聚合元素，则返回所有元素
      const features = this.map.forEachFeatureAtPixel(
        e.pixel,
        (feature) => feature.get("features")
      );
      const length = features && features.length || 0;
      const hasOverlay = this.userOverlay.getPosition() ||
        this.clusterOverlay.getPosition() || this.emptyOverlay.getPosition();
      this.userOverlay.setPosition(undefined);
      this.clusterOverlay.setPosition(undefined);
      this.emptyOverlay.setPosition(undefined);
      if (!length) {
        if (hasOverlay) return;
        this.curUser = {
          ...(this.curUserId ? this.curUser : {}),
          longitude: +e.coordinate[0].toFixed(6),
          latitude: +e.coordinate[1].toFixed(6),
        };
        this.emptyOverlay.setPosition(e.coordinate);
      } else if (length === 1) {
        this.curUser = features[0].get("data");
        this.userOverlay.setPosition(e.coordinate);
      } else {
        this.userList = features.map((feature) => feature.get("data"));
        this.clusterOverlay.setPosition(e.coordinate);
      }
    },
    onMount(map) {
      this.map = map;
      this.userOverlay = new Overlay({
        element: this.$refs.userPopup,
        positioning: "top-left",
        offset: [15, -20],
        autoPan: true,
      });
      this.map.addOverlay(this.userOverlay);
      this.clusterOverlay = new Overlay({
        element: this.$refs.clusterPopup,
        positioning: "top-left",
        offset: [10, -20],
        autoPan: true,
      });
      this.map.addOverlay(this.clusterOverlay);
      this.emptyOverlay = new Overlay({
        element: this.$refs.emptyPopup,
        positioning: "top-left",
        offset: [10, -20],
        autoPan: true,
      });
      this.map.addOverlay(this.emptyOverlay);
      this.map.on("singleclick", this.mapClickHandle);
      this.drawCluster();
    },
    async getUsersList() {
      this.userOverlay && (this.userOverlay.setPosition(undefined));
      this.emptyOverlay && (this.emptyOverlay.setPosition(undefined));
      try {
        const { returnCode, data } = await getAllUsers() || {};
        if (returnCode === 0) {
          this.allUsers = data || [];
          this.drawCluster();
        }
      } catch (err) {
        this.$Message.error(`获取客户列表失败：${err.message || err}`);
      }
    },
  },
  created() {
    this.getUsersList();
  },
  beforeUnmount() {
    this.map.un("singleclick", this.mapClickHandle);
  },
};
</script>

<style lang="less" scoped>
.list-mode {
  position: absolute;
  top: 10px;
  right: 12px;
}
.select-user {
  position: absolute;
  top: 10px;
  left: 50px;
  width: 200px;
}
.popup {
  border-radius: 4px;
  background: #fff;
  width: 150px;
  padding-bottom: 10px;
  border: 1px solid #DDD;
  position: relative;
  &::before {
    content: "";
    position: absolute;
    top: 10px;
    left: -8px;
    width: 0;
    height: 0;
    border-right: 8px solid #DDD;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
  }
  &::after {
    content: "";
    position: absolute;
    top: 10px;
    left: -7px;
    width: 0;
    height: 0;
    border-right: 8px solid #fff;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
  }
  &-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 10px;
    border-bottom: 1px solid #DDD;
    .title {
      flex: 1;
      font-weight: 600;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    .close {
      margin-left: 15px;
      cursor: pointer;
    }
  }
  &-content {
    margin-top: 10px;
    padding: 0 10px;
    max-height: 150px;
    overflow: auto;
  }
}
</style>
